export function initEuropeMap () {
  document.querySelectorAll("[data-map-pos-left]").forEach(el => {
    const left = el.getAttribute("data-map-pos-left");
    const top = el.getAttribute("data-map-pos-top");
    
    if (left && top) {
      el.style.position = "absolute";  // Ensure absolute positioning
      el.style.left = left + "%";
      el.style.top = top + "%";
    }
  });
}